.support {
  display: none;
}

.shopping-list-title {
  color: var(--text-color);
  letter-spacing: -1.28px;
  margin-bottom: 120px;
  font-size: 32px;
  font-weight: 700;
  line-height: 38px;
}

.s-l-title {
  color: var(--violet);
}

.text-shop-list {
  color: #0009;
  text-align: center;
  letter-spacing: -.28px;
  margin-bottom: 14px;
  line-height: 18px;
}

.shop-section {
  padding-top: 40px;
}

.message {
  width: 265px;
  height: 248px;
  margin: 0 auto;
}

.shopping-card {
  background: #fff;
  border: 2px solid #4f2ee866;
  border-radius: 16px;
  padding: 14px;
  display: flex;
  position: relative;
}

.shopping-descr {
  display: flex;
}

.shopping-image {
  border-radius: 8px;
  width: 100px;
  height: 142px;
  margin-right: 14px;
}

.books-sh-container {
  width: 193px;
}

.shop-title {
  color: var(--text-color);
  letter-spacing: -.64px;
  text-transform: uppercase;
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.shopping-category {
  color: var(--top-category-title);
  letter-spacing: .36px;
  margin-bottom: 4px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
}

.shopping-description {
  color: var(--text-color);
  text-overflow: ellipsis;
  whitespace: nowrap;
  letter-spacing: -.56px;
  width: 193px;
  margin-bottom: 16px;
  font-family: DM Sans;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  overflow: hidden;
}

.shopping-author {
  color: var(--top-category-title);
  letter-spacing: -.48px;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px;
}

.delete-button {
  background-color: #4f2ee8;
  border: none;
  border-radius: 50px;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 14px;
  right: 14px;
}

.shopping-svg {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (min-width: 768px) {
  .shopping-list-title {
    letter-spacing: -1.92px;
    margin-bottom: 140px;
    font-size: 48px;
    line-height: 52px;
  }

  .text-shop-list {
    letter-spacing: -.36px;
    margin-bottom: 20px;
    font-size: 18px;
    line-height: 24px;
  }

  .message {
    width: 349px;
    height: 309px;
  }

  .shop-img {
    width: 322px;
    height: 241px;
    margin: 0 auto;
  }
}

@media screen and (min-width: 1440px) {
  .support {
    display: block;
  }

  .shopping-list {
    display: flex;
  }

  .message {
    margin: 0 auto;
  }
}

